/**
 * HACK HACK HACK
 * Align glyphicons with the zoom slider
 */
@-moz-document url-prefix() {
  .infoRow .imageControls .glyphicon {
    top: -18px;
  }
}
.relatedThumbContainerImage {
  z-index: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 100%;
  width: auto;
  cursor: pointer;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.relatedThumbHighlight {
  border: 2px red solid;
}
.relatedThumbToggle {
  height: 75px;
  width: 100%;
  margin-top: 2px;
  margin-bottom: 2px;
  z-index: 1;
}
.relatedThumbToggle .relatedThumbContainer {
  z-index: 1;
  position: relative;
  width: 75px;
  height: 75px;
  overflow: hidden;
  padding: 0px;
}
.relatedThumbToggle .relatedThumbTitle {
  display: none;
}
.relatedThumbToggle:hover .relatedThumbContainer {
  height: 120px;
  width: 120px;
  left: -10px;
  top: -5px;
  position: absolute;
  z-index: 10;
  padding: 5px;
  border-left: 1px solid #000;
  border-right: 1px solid #000;
  border-top: 1px solid #000;
  background-color: white;
  margin: 0px;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  cursor: pointer;
}
.relatedThumbToggle:hover .relatedThumbContainer .relatedThumbContainerImage {
  display: block;
}
.relatedThumbToggle:hover .relatedThumbTitle {
  display: block;
  z-index: 20;
  background-color: white;
  width: 120px;
  left: 5px;
  top: 95px;
  position: absolute;
  padding-left: 3px;
  border-left: 1px solid #000;
  border-right: 1px solid #000;
  border-bottom: 1px solid #000;
}
