  color: #175a85;
  padding-top: 6px;
  white-space: nowrap;
  overflow: hidden;
  /* "overflow" value must be different from "visible" */
  text-overflow: ellipsis;
}
.resultContainer h5:hover {
  overflow: visible;
  white-space: normal;
  height: auto;
}
.previewCrop {
  position: relative;
