  height: 150px;
  overflow: hidden;
  background-color: #d9dee2;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  vertical-align: middle;
}
.previewCrop .badge {
  position: absolute;
  right: 5px;
  top: 5px;
}
.previewCrop:before {
  content: ' ';
  display: inline-block;
  vertical-align: middle;
