}
.videoColumn {
  height: 480px;
}
.imageContainer {
  height: 100%;
  width: 100%;
  max-height: inherit;
}
.imageContent {
  margin-left: auto;
  margin-right: auto;
  display: block;
  max-height: inherit;
}
.fullscreenImageContainer {
  max-height: 550px;
}
.fixedHeightContainer {
  height: 550px;
}
.imageFullscreen {
  background-color: black;
}
.canvasFullscreen {
  background-color: black;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.hoverSlider {
  -moz-opacity: 0;
  -khtml-opacity: 0;
  -webkit-opacity: 0;
  opacity: 0;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=0);
  filter: alpha(opacity=0);
  width: 85%;
  position: absolute;
  right: 7.5%;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(211, 211, 211, 0.7);
  padding: 5px;
  bottom: 30px;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  display: inline;
}
.hoverSlider input[type='range'] {
  width: calc(93% - 50px);
}
.hoverSlider .canFullscreen {
  top: 40%;
  position: absolute;
}
.fullscreenImageContainer:hover .hoverSlider {
  -moz-opacity: 1;
  -khtml-opacity: 1;
  -webkit-opacity: 1;
  opacity: 1;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(opacity=100);
  filter: alpha(opacity=100);
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
/**
 * 3d viewer
 */
