    height: 600px;
  }
}
/**
 * Box.net viewer
 */
.boxContainer {
  height: 500px;
}
.box-controls-container {
  height: 44px;
  line-height: 44px;
  top: 10;
  width: 100%;
  text-align: center;
}
.box-controls-container .controls {
  display: inline-block;
}
.box-controls-container .controls-left {
  display: block;
  position: absolute;
  left: 0;
}
.box-controls-container .controls-right {
  position: absolute;
  right: 0;
}
.box-controls-container .controls .btn {
  border: 0;
  width: 30px;
  height: 30px;
  padding: 0;
  cursor: pointer;
}
.box-controls-container .controls .page {
  display: inline-block;
  position: relative;
  vertical-align: top;
}
.box-controls-container .controls .page-display {
  width: auto;
  background: transparent;
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  font-size: 10pt;
  line-height: 0;
  margin: 0 5px 0;
  vertical-align: middle;
  text-align: center;
}
.box-controls-container .controls .page-input {
  position: absolute;
  width: 100%;
  height: 24px;
  line-height: 24px;
  top: 11px;
  left: 0;
  display: none;
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  font-size: 10pt;
  text-align: center;
  vertical-align: middle;
}
.box-controls-container .controls .page-input:focus {
  outline: 0;
  border: 1px solid #aaa;
  box-shadow: #ccc 0 0 1px inset;
}
.box-controls-container .controls .btn,
.box-controls-container .controls {
  background-color: transparent;
  display: inline-block;
  vertical-align: top;
  margin: 7px 5px 0;
}
.box-controls-container .controls .icon {
  background-color: transparent;
  background-position: center center;
  background-repeat: no-repeat;
}
.box-controls-container .controls .btn .icon {
  width: 100%;
  height: 100%;
  display: inline-block;
  margin: 0;
  padding: 0;
}
.box-controls-container .controls .btn {
  cursor: pointer;
}
.box-controls-container .controls .btn:disabled {
  cursor: default;
}
